<script>
	import { LayerCake, Svg, WebGL, Html } from 'layercake';

	import Plot from './Plot.svelte';
	import AxisX from './AxisX.svelte';
	import AxisY from './AxisY.svelte';
	import QuadTree from './QuadTree.svelte';
	
	export let data;
	export let diameter = 1;
	export let colors = [[128, 128, 128]];
	export let categories = null;
	export let selected = null;
	export let fixed = false;

	export let xKey = 'x';
	export let yKey = 'y';
	export let catKey = 'group';
	
	$: xDomain = fixed ? [0, 1000] : null;
	$: yDomain = fixed ? [-100, 200] : null;
	
	const padding = 6;
</script>

<style>
	.chart-container {
		margin-top: 10px;
		margin-left: 30px;
		width: calc(100% - 50px);
		height: calc(100% - 130px);
	}
	.tooltip {
		position: absolute;
		display: block;
		background-color: rgba(255, 255, 255, 0.7);
		padding: 5px;
		color: #000;
		min-width: 100px;
		line-height: 1;
	}
	.circle {
		position: absolute;
		border-radius: 50%;
		transform: translate(-50%, -50%);
		pointer-events: none;
		width: 10px;
		height: 10px;
		border: 2px solid #000;
	}
</style>

<div class="chart-container">
	<LayerCake
		padding={{ top: 10, right: 5, bottom: 20, left: 25 }}
		x={xKey}
		y={yKey}
		{xDomain}
		{yDomain}
		xPadding={[padding, padding]}
		yPadding={[padding, padding]}
		{data}
	>
		<Svg>
			<AxisX
				ticks={5}
			/>
			<AxisY
				ticks={5}
			/>
		</Svg>

		<WebGL>
			<Plot
				{diameter}
				{colors}
				{categories}
				{catKey}
				{selected}
			/>
		</WebGL>
		
		<Html>
			<QuadTree
				let:x
				let:y
				let:visible
				let:found
				{selected}
			>
				<div
					class="circle"
					style="top:{y}px;left:{x}px;display: { visible ? 'block' : 'none' }; width: {(Math.sqrt(found.population) / 40) + 6}px ; height: {(Math.sqrt(found.population) / 40) + 6}px;"
				></div>
				<div
					class="tooltip"
					style="top:{y+ 5}px;left:{x + 10}px;display: { visible ? 'block' : 'none' };"
		><b>{found.name}</b><br /><small>{found[catKey]}<br/>{Number(found[xKey]).toFixed(1)} new cases per 100k<br/>{#if Number(found[yKey]) > 0}+{/if}{Number(found[yKey]).toFixed(0)}% change, week on week</small></div>
			</QuadTree>
		</Html>
	</LayerCake>
</div>