import { csvParse, autoType } from 'd3-dsv';

export async function getLookup(url) {
  let response = await fetch(url);
  let string = await response.text();
	let rawdata = await csvParse(string, autoType);
	
	let lookup = {};
	rawdata.forEach(d => {
		lookup[d.code] = d;
	});

	return lookup;
}

export async function getData(url, lookup) {
  let response = await fetch(url);
  let string = await response.text();
	let rawdata = await csvParse(string, d => {
  	return {
			code: d.areaCode,
			name: d.areaName,
    	datestring: d.date,
			date: new Date(d.date),
    	rate: +d.newCasesByPublishDateRollingRate,
    	change: +d.newCasesByPublishDateChangePercentage
  	};
	});
	
	// Calculate the number of days of data
	let startdate = new Date('2020-01-02');
	let days = (rawdata[0].date - startdate) / 86400000;
	
	// Remove redundant days
	let newdata = [];
	let count = 0;
	rawdata.forEach(d => {
		if (count >= 1 && count <= days - 120) {
			newdata.push(d);
		}
		count ++;
		if (count >= days) {
			count = 0;
		}
	});

	newdata.sort((a, b) => a.name.localeCompare(b.name));
	
	let dates = [];
	for (let i = 0; i <= days - 121; i ++) {
		dates.push(newdata[i].datestring);
	}
	dates.reverse();
	
	// Create pivoted dataset
	let indexed = {};
	newdata.forEach(d => {
		if (lookup[d.code]) {
			if (!Object.keys(indexed).includes(d.code)) {
				indexed[d.code] = {
					code: d.code,
					name: d.name,
					group: lookup[d.code].group,
					population: lookup[d.code].population
				};
			}
			indexed[d.code][d.datestring + '_' + 'rate'] = d.rate;
			indexed[d.code][d.datestring + '_' + 'change'] = d.change;
		}
	});
	
	// Create final data array
	let data = [];
	for (let i in indexed) {
		data.push(indexed[i]);
	}

  return [data, dates];
}