<script>
	import ScatterChart from "./ScatterChart.svelte";
	import { getLookup, getData } from "./utils.js";
	import Select from "./Select.svelte";
	import Switch from "./Switch.svelte";

	const csvurl =
		"https://bothness.github.io/covid-scatter/data/code-name-pop-tier.csv";
	const apiurl =
		"https://bothness.github.io/covid-scatter/data/data-static.csv";
	const catKey = "group";

	const regions = [
		{ code: "Tier 1", name: "England (Tier 1)" },
		{ code: "Tier 2", name: "England (Tier 2)" },
		{ code: "Tier 3", name: "England (Tier 3)" },
		{ code: "Scotland", name: "Scotland" },
		{ code: "Wales", name: "Wales" },
		{ code: "Northern Ireland", name: "Northern Ireland" },
	];
	const categories = regions.map((obj) => obj.code);
	const colors = [
		[39, 160, 204],
		[32, 96, 149],
		[0, 60, 87],
		[17, 140, 123],
		[135, 26, 91],
		[168, 189, 58],
	];

	let data;

	let keys;
	let places;
	let selected;
	let place = null;
	let region = null;
	let xKey;
	let yKey;

	let value;
	let fixed = true;

	getLookup(csvurl).then((lookup) => {
		getData(apiurl, lookup).then((result) => {
			data = result[0];
			keys = result[1];
			value = keys.length - 1;
		});
	});

	$: if (data && keys && value) {
		xKey = keys[value] + "_rate";
		yKey = keys[value] + "_change";
	}

	function selectRegion() {
		console.log(region);
		selected = region ? { value: region.code, col: "group" } : null;
		place = null;
	}
	function selectPlace() {
		selected = place ? { value: place.code, col: "code" } : null;
		region = null;
	}
</script>

<style>
	.label {
		position: relative;
		top: 5px;
	}
	.slider {
		-webkit-appearance: none;
		padding: 0;
		width: 100%;
		height: 10px !important;
		border-radius: 5px;
		background: #d3d3d3;
		outline: none;
	}
	.slider::-webkit-slider-thumb {
		-webkit-appearance: none;
		appearance: none;
		width: 30px;
		height: 30px;
		border-radius: 50%;
		background: #206095;
		cursor: pointer;
		border: none;
	}
	.slider::-moz-range-thumb {
		width: 30px;
		height: 30px;
		border-radius: 50%;
		background: #206095;
		cursor: pointer;
		border: none;
	}
	.grid {
		display: grid;
		width: 100%;
		grid-gap: 10px;
		grid-template-columns: auto 140px;
		grid-auto-flow: row;
	}
	.select {
		display: inline-block;
		width: calc(50% - 3px);
		min-width: 260px;
		margin-bottom: 5px;
	}
	.bottom {
		align-self: end;
		margin-bottom: 12px;
	}
	.date {
		position: relative;
		top: -12px;
		box-sizing: border-box;
		padding: 5px;
		text-align: center;
		height: 34px;
		border-radius: 34px;
		font-weight: bold;
		background-color: #206095;
		color: #fff;
	}
	h1 {
		margin-bottom: 0;
	}
	.subtitle {
		margin-top: 0;
		padding-bottom: 10px;
		font-weight: bold;
	}
	.label-left {
		position: absolute;
		display: block;
		width: 300px;
		transform-origin: 0 25px;
  	transform: rotate(90deg);
	}
	.label-bottom {
		width: 100%;
		text-align: right;
	}
</style>

<h1>UK Covid-19 Infections</h1>
<p class="subtitle">New cases per 100,000 people vs % change in new cases since previous week</p>

<div class="grid">
	{#if data && keys}
		<div>
			<input
				type="range"
				min="0"
				max={keys.length - 1}
				bind:value
				class="slider" />
		</div>
		<div class="date">
			{new Date(keys[value]).toLocaleDateString('en-GB', {
				year: 'numeric',
				month: 'short',
				day: 'numeric',
			})}
		</div>
	{/if}

	<div>
		<div class="select">
			<Select
				options={regions}
				bind:selected={region}
				search={true}
				on:select={selectRegion}
				placeholder="Filter by nation/tier" />
		</div>
		{#if data}
			<div class="select">
				<Select
					options={data}
					bind:selected={place}
					search={true}
					on:select={selectPlace}
					placeholder="Filter by authority" />
			</div>
		{/if}
	</div>
	<div class="bottom">
		<Switch bind:checked={fixed} />
		<span class="label">Fix axes</span>
	</div>
</div>

{#if xKey && yKey}
	<div class='label-left'>&larr; % increase week-on-week</div>
	<ScatterChart
		diameter={6}
		{data}
		{xKey}
		{yKey}
		{catKey}
		{categories}
		{colors}
		{selected}
		{fixed} />
		<div class='label-bottom'>Weekly cases per 100k people &rarr;&nbsp;&nbsp;</div>
{/if}

<p>
	<small>Cases data from
		<a href="https://coronavirus.data.gov.uk/details/cases" target="_blank">UK
			Government Covid-19 Dashboard</a>. New cases per 100k people, by publication date (7 day rolling avg). Population data (2019) from the
		<a
			href="https://www.ons.gov.uk/peoplepopulationandcommunity/populationandmigration/populationestimates/datasets/populationestimatesforukenglandandwalesscotlandandnorthernireland"
			target="_blank">Office for National Statistics</a>. Coded by
		<a href="https://twitter.com/bothness" target="_blank">Ahmad Barclay</a>
		using Svelte, LayerCake and regl-tween. Get the
		<a
			href="https://svelte.dev/repl/06d779cba58c4cc99cbc4ef63b993a30?version=3.30.0"
			target="_blank">code here</a>.</small>
</p>
